import {Button, Form, Space} from "antd";
import React from "react";
import {withTranslation} from "react-i18next";
import {InboxOutlined} from "@ant-design/icons";
import Dragger from "antd/lib/upload/Dragger";
import {isValueEmpty} from "../../../../Utils/empty";
import apiAddress from "../../../../Utils/API/apiAddress";
import {fileUpload, fileUploadWithoutMD5} from "../../../../Utils/fileUpload";

interface ItemUploadPropsType{
    label: string       // Form 
    name: string        // Form 
    required: boolean   // Form 
    accept: string      // 
    use: "user" | "admin"       // 
    downloadFilename?: string    // 
    downloadFileSuffix: string  // 
}

const ItemUpload = (props: ItemUploadPropsType & any) => {

    return (
        <>
            <Form.Item label={props.label ?? ""} name={props.name} rules={[{required: props.required}]}>
                <UploadFile {...props}/>
            </Form.Item>
        </>
    )
}



const UploadFile = (props: any) => {

    const {value, onChange} = props

    let nameList: string[] = []
    for (let nm of props.accept.split(",")) {
        nameList.push("*" + nm)
    }

    const callback = (value: any) => {
        onChange(value.id)
    }
    const upload = (file: any) => {
        if (props.ues === "user")
            fileUploadWithoutMD5(file, callback)
        else fileUpload([file], callback)
    }

    const filename = (props.downloadFilename ?? value) + props.downloadFileSuffix;

    return (
        <>
            {!isValueEmpty(value) && (
                <div style={{marginBottom: 24}}>
                    <div>  </div>
                    <Space>
                        {filename}
                        <Button size={"small"} onClick={() => {
                            const path = apiAddress().CLIENT_SERVER + "/api/filesys/download/" + value + "/" + filename
                            window.open(path)
                        }}></Button>
                        <Button danger size={"small"} onClick={() => {
                            onChange(null)
                        }}></Button>
                    </Space>

                </div>
            )}
            <Dragger
                multiple={false}
                accept={props.accept}
                action=""
                listType="text"
                beforeUpload={upload}
                showUploadList={false}
            >
                <p className="ant-upload-drag-icon">
                    <InboxOutlined/>
                </p>
                <p className="ant-upload-text"></p>
                <p className="ant-upload-hint">
                     {nameList} 
                </p>
            </Dragger>
        </>
    )
}

export default withTranslation()(ItemUpload)
